'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { getOfflineManager } from '@/lib/offline/offline-manager'
import { getSyncEngine, SyncStatus } from '@/lib/offline/sync-engine'
import { Database, CheckCircle, XCircle, Loader2, Trash2, RefreshCw } from 'lucide-react'

interface TestResult {
  name: string
  status: 'pending' | 'success' | 'error'
  message?: string
  duration?: number
}

export default function TestOfflinePage() {
  const [results, setResults] = useState<TestResult[]>([])
  const [isRunning, setIsRunning] = useState(false)
  const [isSyncing, setIsSyncing] = useState(false)
  const [stats, setStats] = useState<any>(null)
  const [syncStatus, setSyncStatus] = useState<SyncStatus | null>(null)
  const [offlineManager, setOfflineManager] = useState<any>(null)
  const [syncEngine, setSyncEngine] = useState<any>(null)

  useEffect(() => {
    // Âè™Âú®ÂÆ¢Êà∂Á´ØÂàùÂßãÂåñ OfflineManager Âíå SyncEngine
    if (typeof window !== 'undefined') {
      setOfflineManager(getOfflineManager())
      setSyncEngine(getSyncEngine())
    }
  }, [])

  const updateResult = (name: string, status: TestResult['status'], message?: string, duration?: number) => {
    setResults(prev => {
      const existing = prev.find(r => r.name === name)
      if (existing) {
        return prev.map(r => r.name === name ? { ...r, status, message, duration } : r)
      }
      return [...prev, { name, status, message, duration }]
    })
  }

  const runTests = async () => {
    if (!offlineManager) {
      alert('OfflineManager Â∞öÊú™ÂàùÂßãÂåñ')
      return
    }

    setIsRunning(true)
    setResults([])

    try {
      // Test 1: Âª∫Á´ãË≥áÊñô
      updateResult('Âª∫Á´ã Tour Ë≥áÊñô', 'pending')
      const startCreate = Date.now()
      const tour = await offlineManager.create('tours', {
        code: 'TEST001',
        name: 'Ê∏¨Ë©¶Ë°åÁ®ã',
        destination: 'Âè∞Âåó',
        startDate: '2025-01-10',
        endDate: '2025-01-15',
        days: 5,
        nights: 4,
        adultCount: 10,
        childCount: 2,
        infantCount: 0,
        status: 'planning',
        totalCost: 50000,
        totalRevenue: 80000,
        profitMargin: 30000
      })
      updateResult('Âª∫Á´ã Tour Ë≥áÊñô', 'success', `ID: ${tour.id}`, Date.now() - startCreate)

      // Test 2: ËÆÄÂèñË≥áÊñô
      updateResult('ËÆÄÂèñ Tour Ë≥áÊñô', 'pending')
      const startRead = Date.now()
      const readTour = await offlineManager.get('tours', tour.id)
      if (readTour && readTour.id === tour.id) {
        updateResult('ËÆÄÂèñ Tour Ë≥áÊñô', 'success', `ÂêçÁ®±: ${readTour.name}`, Date.now() - startRead)
      } else {
        updateResult('ËÆÄÂèñ Tour Ë≥áÊñô', 'error', 'Ë≥áÊñô‰∏çÁ¨¶')
      }

      // Test 3: Êõ¥Êñ∞Ë≥áÊñô
      updateResult('Êõ¥Êñ∞ Tour Ë≥áÊñô', 'pending')
      const startUpdate = Date.now()
      const updatedTour = await offlineManager.update('tours', tour.id, {
        name: 'Ê∏¨Ë©¶Ë°åÁ®ã (Â∑≤Êõ¥Êñ∞)',
        adultCount: 15
      })
      if (updatedTour.name === 'Ê∏¨Ë©¶Ë°åÁ®ã (Â∑≤Êõ¥Êñ∞)' && updatedTour.adultCount === 15) {
        updateResult('Êõ¥Êñ∞ Tour Ë≥áÊñô', 'success', `ÁâàÊú¨: ${updatedTour.version}`, Date.now() - startUpdate)
      } else {
        updateResult('Êõ¥Êñ∞ Tour Ë≥áÊñô', 'error', 'Êõ¥Êñ∞Â§±Êïó')
      }

      // Test 4: Êü•Ë©¢ÊâÄÊúâË≥áÊñô
      updateResult('Êü•Ë©¢ÊâÄÊúâ Tours', 'pending')
      const startGetAll = Date.now()
      const allTours = await offlineManager.getAll('tours')
      updateResult('Êü•Ë©¢ÊâÄÊúâ Tours', 'success', `ÂÖ± ${allTours.length} Á≠Ü`, Date.now() - startGetAll)

      // Test 5: ‰ΩøÁî®Á¥¢ÂºïÊü•Ë©¢
      updateResult('Á¥¢ÂºïÊü•Ë©¢ (status)', 'pending')
      const startIndex = Date.now()
      const planningTours = await offlineManager.getByIndex('tours', 'status', 'planning')
      updateResult('Á¥¢ÂºïÊü•Ë©¢ (status)', 'success', `ÊâæÂà∞ ${planningTours.length} Á≠Ü`, Date.now() - startIndex)

      // Test 6: Ê™¢Êü•ÂêåÊ≠•‰ΩáÂàó
      updateResult('Ê™¢Êü•ÂêåÊ≠•‰ΩáÂàó', 'pending')
      const startSync = Date.now()
      const pendingSync = await offlineManager.getPendingSyncItems()
      updateResult('Ê™¢Êü•ÂêåÊ≠•‰ΩáÂàó', 'success', `ÂæÖÂêåÊ≠•: ${pendingSync.length} Á≠Ü`, Date.now() - startSync)

      // Test 7: Âà™Èô§Ë≥áÊñô
      updateResult('Âà™Èô§ Tour Ë≥áÊñô', 'pending')
      const startDelete = Date.now()
      await offlineManager.delete('tours', tour.id)
      const deletedTour = await offlineManager.get('tours', tour.id)
      if (!deletedTour) {
        updateResult('Âà™Èô§ Tour Ë≥áÊñô', 'success', 'Â∑≤ÊàêÂäüÂà™Èô§', Date.now() - startDelete)
      } else {
        updateResult('Âà™Èô§ Tour Ë≥áÊñô', 'error', 'Âà™Èô§Â§±Êïó')
      }

      // Test 8: ÊâπÊ¨°Âª∫Á´ã
      updateResult('ÊâπÊ¨°Âª∫Á´ã 3 Á≠ÜË≥áÊñô', 'pending')
      const startBatch = Date.now()
      const batchData = await offlineManager.createBatch('tours', [
        { code: 'BATCH001', name: 'ÊâπÊ¨°Ê∏¨Ë©¶1', destination: 'Âè∞Âåó', startDate: '2025-02-01', endDate: '2025-02-05', days: 4, nights: 3, adultCount: 10, childCount: 0, infantCount: 0, status: 'planning', totalCost: 40000, totalRevenue: 60000, profitMargin: 20000 },
        { code: 'BATCH002', name: 'ÊâπÊ¨°Ê∏¨Ë©¶2', destination: 'Âè∞‰∏≠', startDate: '2025-03-01', endDate: '2025-03-05', days: 4, nights: 3, adultCount: 8, childCount: 2, infantCount: 0, status: 'planning', totalCost: 35000, totalRevenue: 55000, profitMargin: 20000 },
        { code: 'BATCH003', name: 'ÊâπÊ¨°Ê∏¨Ë©¶3', destination: 'È´òÈõÑ', startDate: '2025-04-01', endDate: '2025-04-05', days: 4, nights: 3, adultCount: 12, childCount: 1, infantCount: 0, status: 'planning', totalCost: 45000, totalRevenue: 70000, profitMargin: 25000 }
      ])
      updateResult('ÊâπÊ¨°Âª∫Á´ã 3 Á≠ÜË≥áÊñô', 'success', `Âª∫Á´ã‰∫Ü ${batchData.length} Á≠Ü`, Date.now() - startBatch)

      // Êõ¥Êñ∞Áµ±Ë®àË≥áÊñô
      const newStats = await offlineManager.getStats()
      setStats(newStats)

    } catch (error) {
      console.error('Ê∏¨Ë©¶Â§±Êïó:', error)
      updateResult('Ê∏¨Ë©¶Âü∑Ë°å', 'error', error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§')
    } finally {
      setIsRunning(false)
    }
  }

  const clearAllData = async () => {
    if (!offlineManager) return
    if (!confirm('Á¢∫ÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÊ∏¨Ë©¶Ë≥áÊñôÂóéÔºü')) return

    try {
      await offlineManager.clear('tours')
      await offlineManager.clearCompletedSync()
      const newStats = await offlineManager.getStats()
      setStats(newStats)
      setResults([])
      alert('Â∑≤Ê∏ÖÁ©∫ÊâÄÊúâÊ∏¨Ë©¶Ë≥áÊñô')
    } catch (error) {
      alert('Ê∏ÖÁ©∫Â§±Êïó: ' + (error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'))
    }
  }

  const handleSync = async () => {
    if (!syncEngine) {
      alert('SyncEngine Â∞öÊú™ÂàùÂßãÂåñ')
      return
    }

    setIsSyncing(true)
    try {
      const status = await syncEngine.manualSync()
      setSyncStatus(status)

      // Êõ¥Êñ∞Áµ±Ë®àË≥áÊñô
      if (offlineManager) {
        const newStats = await offlineManager.getStats()
        setStats(newStats)
      }

      if (status.completedCount > 0) {
        alert(`‚úÖ ÂêåÊ≠•ÂÆåÊàêÔºö${status.completedCount} Á≠ÜÊàêÂäü`)
      } else if (status.pendingCount === 0) {
        alert('‚úÖ Ê≤íÊúâÂæÖÂêåÊ≠•È†ÖÁõÆ')
      }
    } catch (error) {
      alert('ÂêåÊ≠•Â§±Êïó: ' + (error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'))
    } finally {
      setIsSyncing(false)
    }
  }

  useEffect(() => {
    // ËºâÂÖ•ÂàùÂßãÁµ±Ë®àË≥áÊñôÂíåÂêåÊ≠•ÁãÄÊÖã
    if (offlineManager) {
      offlineManager.getStats().then(setStats)
    }
    if (syncEngine) {
      syncEngine.getStatus().then(setSyncStatus)
    }
  }, [offlineManager, syncEngine])

  return (
    <div className="container mx-auto p-6 max-w-6xl">
      <div className="mb-6">
        <h1 className="text-3xl font-bold flex items-center gap-2">
          <Database className="h-8 w-8" />
          Èõ¢Á∑öÊû∂ÊßãÊ∏¨Ë©¶
        </h1>
        <p className="text-muted-foreground mt-2">
          Ê∏¨Ë©¶ OfflineManager ÁöÑ CRUD Êìç‰Ωú„ÄÅÁ¥¢ÂºïÊü•Ë©¢„ÄÅÂêåÊ≠•‰ΩáÂàóÁ≠âÂäüËÉΩ
        </p>
      </div>

      <div className="grid gap-6 md:grid-cols-2 mb-6">
        <Card>
          <CardHeader>
            <CardTitle>Ë≥áÊñôÁµ±Ë®à</CardTitle>
            <CardDescription>IndexedDB ÂÑ≤Â≠òÁãÄÊÖã</CardDescription>
          </CardHeader>
          <CardContent>
            {stats ? (
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span className="text-muted-foreground">Tours:</span>
                  <span className="font-mono">{stats.tours || 0} Á≠Ü</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-muted-foreground">Orders:</span>
                  <span className="font-mono">{stats.orders || 0} Á≠Ü</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-muted-foreground">Quotes:</span>
                  <span className="font-mono">{stats.quotes || 0} Á≠Ü</span>
                </div>
                <div className="flex justify-between border-t pt-2">
                  <span className="text-muted-foreground">ÂæÖÂêåÊ≠•:</span>
                  <span className="font-mono font-semibold">{stats.pendingSync || 0} Á≠Ü</span>
                </div>
              </div>
            ) : (
              <div className="text-muted-foreground">ËºâÂÖ•‰∏≠...</div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Ê∏¨Ë©¶ÊéßÂà∂</CardTitle>
            <CardDescription>Âü∑Ë°åÊ∏¨Ë©¶ÊàñÊ∏ÖÁ©∫Ë≥áÊñô</CardDescription>
          </CardHeader>
          <CardContent className="space-y-3">
            <Button
              onClick={runTests}
              disabled={isRunning}
              className="w-full"
              size="lg"
            >
              {isRunning ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Ê∏¨Ë©¶Âü∑Ë°å‰∏≠...
                </>
              ) : (
                'ÈñãÂßãÊ∏¨Ë©¶'
              )}
            </Button>
            <Button
              onClick={clearAllData}
              variant="destructive"
              className="w-full"
              size="lg"
            >
              <Trash2 className="mr-2 h-4 w-4" />
              Ê∏ÖÁ©∫ÊâÄÊúâË≥áÊñô
            </Button>
          </CardContent>
        </Card>
      </div>

      {results.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Ê∏¨Ë©¶ÁµêÊûú</CardTitle>
            <CardDescription>ÂÖ± {results.length} ÂÄãÊ∏¨Ë©¶È†ÖÁõÆ</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {results.map((result, index) => (
                <div
                  key={index}
                  className="flex items-center justify-between p-3 border rounded-lg"
                >
                  <div className="flex items-center gap-3">
                    {result.status === 'pending' && (
                      <Loader2 className="h-5 w-5 animate-spin text-blue-500" />
                    )}
                    {result.status === 'success' && (
                      <CheckCircle className="h-5 w-5 text-green-500" />
                    )}
                    {result.status === 'error' && (
                      <XCircle className="h-5 w-5 text-red-500" />
                    )}
                    <div>
                      <div className="font-medium">{result.name}</div>
                      {result.message && (
                        <div className="text-sm text-muted-foreground">{result.message}</div>
                      )}
                    </div>
                  </div>
                  {result.duration !== undefined && (
                    <div className="text-sm text-muted-foreground font-mono">
                      {result.duration}ms
                    </div>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
